class Ship {
  // inherit from Planet if we decide to give this gravitational pull (comets trailing?)
  constructor(radius, pos) {
    this.radius = radius;
    this.pos = pos;
  }

  launch(){

  }

  draw(ctx){
    ctx.fillStyle = 'rgb(100,100,100)';
    ctx.beginPath();
    var [x, y] = this.pos;
    ctx.arc(x, y ,this.radius,0,2*Math.PI);
    ctx.closePath();
    ctx.fill();
  }

}

module.exports = window.Ship = Ship;
